<template>
    <div class="form__counter">
        <button type="button" aria-label="Убрать один товар" @click="deleteOneProduct">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <input type="number" min="1" v-model.number="localProductAmount">

        <button type="button" aria-label="Добавить один товар" @click="addOneProduct">
            <svg width="12" height="12" fill="currentColor">
                <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>
<script>

export default {
    props: ['productAmount'],
    data() {
        return {
            localProductAmount: this.productAmount,
        };
    },

    watch: {
        productAmount(value) {
            this.localProductAmount = value;
        },
        localProductAmount(value) {
            this.$emit('update:productAmount', value);
        }
    },

    methods: {
        addOneProduct() {
            this.$emit('update:productAmount', this.localProductAmount + 1);
        },

        deleteOneProduct() {
            if (this.localProductAmount > 1) {
                this.$emit('update:productAmount', this.localProductAmount - 1);
            }
        },
    },
}
</script>