<template>
    <div class="cart__block">
        <div v-if="!isLoading">
            <ul class="cart__orders">

                <li class="cart__order" v-for="item in products" :key="item.productId" :item="item">
                    <h3>{{ item.product.title }}</h3>

                    <b>{{ item.product.price | numberFormat }} ₽ x {{ item.amount }}</b>
                    <span>Артикул: {{ item.product.id }}</span>
                </li>
            </ul>

            <div class="cart__total">
                <p>Доставка: <b>500 ₽</b></p>
                <p>Итого: <b>{{ totalAmount }}</b> {{ totalAmount | amountText }} на сумму <b>{{ totalPrice | numberFormat
                }} ₽</b></p>
            </div>
            <button class="cart__button button button--primery" type="submit" v-if="totalAmount > 0">
                Оформить заказ
            </button>
        </div>
        <div v-else>
            <img src="../assets/img/gif/1494.gif" alt="">
        </div>
    </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import amountText from '@/helpers/amountText';

export default {
    props: ['products', 'totalPrice', 'totalAmount'],
    data() {
        return {
            isLoading: true,
        }
    },
    filters: {
        numberFormat,
        amountText
    },
    mounted() {
        setTimeout(() => {
            this.isLoading = false;
        }, 2000);
    }
}
</script>